<template>
  <div class="another-page">
    <h1>Welcome to Another Page</h1>

    <!-- ‚úÖ ReactÏóêÏÑú Î∞õÏùÄ count Í∞í -->
    <p>ReactÏóêÏÑú Ï†ÑÎã¨Îêú count Í∞í: {{ receivedCount }}</p>

    <!-- ‚úÖ VueÏóêÏÑú Í¥ÄÎ¶¨ÌïòÎäî count Í∞í -->
    <p>VueÏóêÏÑú Í¥ÄÎ¶¨ÌïòÎäî count Í∞í: {{ vueCount }}</p>

    <!-- ‚úÖ Vue count Ï¶ùÍ∞Ä Î≤ÑÌäº -->
    <button @click="increaseVueCount">VueÏóêÏÑú count Ï¶ùÍ∞Ä</button>

    <!-- Î©îÏù∏ ÌéòÏù¥ÏßÄÎ°ú ÎèåÏïÑÍ∞ÄÎäî Î≤ÑÌäº -->
    <button @click="goBack">Go Back</button>
  </div>
</template>

<script>
export default {
  name: "AnotherPage",
  data() {
    return {
      receivedCount: 0, // ‚úÖ ReactÏóêÏÑú Ï†ÑÎã¨Îêú count Í∞í
      vueCount: 0, // ‚úÖ VueÏóêÏÑú ÏûêÏ≤¥Ï†ÅÏúºÎ°ú Í¥ÄÎ¶¨ÌïòÎäî count Í∞í
    };
  },
  methods: {
    goBack() {
      this.$router.push("/");
    },
    increaseVueCount() {
      this.vueCount += 1; // ‚úÖ VueÏóêÏÑú count Ï¶ùÍ∞Ä
      window.dispatchEvent(new CustomEvent("vue-count-updated", { detail: this.vueCount }));
    },
    handleReactCountUpdate(event) {
      console.log("üü¢ ReactÏóêÏÑú count Í∞í ÏóÖÎç∞Ïù¥Ìä∏Îê®:", event.detail);
      this.receivedCount = event.detail; // ‚úÖ ReactÏóêÏÑú Ï†ÑÎã¨Îêú count Í∞í Î∞òÏòÅ
    }
  },
  mounted() {
    // ‚úÖ ReactÏóêÏÑú Î≥¥ÎÇ∏ count Í∞í Î∞õÍ∏∞
    window.addEventListener("update-count", this.handleReactCountUpdate);

    // ‚úÖ Vue count Ïú†ÏßÄ (sessionStorage ÏÇ¨Ïö©)
    const savedVueCount = sessionStorage.getItem("vueCount");
    if (savedVueCount !== null) {
      this.vueCount = parseInt(savedVueCount, 10);
    }
  },
  beforeDestroy() {
    // ‚úÖ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Ï†úÍ±∞ (Î©îÎ™®Î¶¨ ÎàÑÏàò Î∞©ÏßÄ)
    window.removeEventListener("update-count", this.handleReactCountUpdate);
  }
};
</script>


<style scoped>
.another-page {
  text-align: center;
  padding: 50px;
}

h1 {
  font-size: 2em;
  color: #333;
}

p {
  font-size: 1.2em;
  margin-bottom: 20px;
}

button {
  font-size: 1em;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #3498db;
  color: white;
  border: none;
  cursor: pointer;
  margin: 10px;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #2980b9;
}
</style>
